
.gpt3__possibility{
    display: flex;
    flex-direction: row;

}
.gpt3__possibility-image{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}

.gpt3__possibility-image img{
    width: 100%;
    height: 100%;


}
.gpt3__possibility-content{
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    flex-direction: column;
}

.gpt3__possibility-content h4{
 font-family: var(--font-family);
 font-size: 16px;
 line-height: 30px;
 font-weight: 500;
 color: aqua;
}
.gpt3__possibility-content h4:last-child{
    
    color: rgb(0, 255, 136);
}
.gpt3__possibility-content h1{
    font-family: var(--font-family);
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    margin: 1rem 0;
}

.gpt3__possibility-content p{
    margin-top: 2rem;
    text-wrap: wrap;
    margin-bottom: 2rem;
}

@media screen and (max-width:950px) {
.gpt3__possibility{
    flex-direction: column;
}
    .gpt3__possibility-image{
        margin: 1rem 0;
    }
    .gpt3__possibility-content{
        margin-top: 2rem;
    }
}